html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  min-height: 100vh;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.card {
  width: 250px;
  color: inherit;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 15px;
  text-decoration: none;
}

.flag {
  border-radius: 5px;
  width: 100%;
  height: auto;
}

.details-container {
  background-color: inherit;
  max-width: 1000px;
  min-height: 100vh;
  margin: 30px auto;
  padding: 20px;
}

.details-content {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 50px;
  display: flex;
}

.details-flag {
  width: 320px;
  height: auto;
  box-shadow: 0 0 10px #0000001a;
}

.details-text {
  max-width: 600px;
  font-size: 16px;
  line-height: 1.7;
}

.back-button {
  color: #000;
  margin-bottom: 30px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
}

.back-button:hover {
  text-decoration: underline;
}

.light {
  color: #111;
  background-color: #f2f2f2;
}

.dark {
  color: #fff;
  background-color: #202c37;
}

.dark .card {
  background-color: #2b3945;
  border: 1px solid #444;
}

.card {
  width: 100%;
  max-width: 250px;
}

.dark .back-button {
  color: #fff;
}

@media (width <= 600px) {
  .details-content {
    flex-direction: column;
    align-items: center;
  }

  .details-flag {
    width: 100%;
    max-width: 300px;
  }

  .details-text {
    text-align: center;
    max-width: 100%;
  }

  .card {
    max-width: 90%;
  }
}
